// import {useState, useEffect} from "react"
import "./Section.css";
import Data from "./Data";
const Section5 = ({title = "Title", data = [] }) => {
    return (
        <div className="Section fav" >
            <div className="div-Section-title ">
                <a className="a-Section-Title">{title}</a>
                <span>Count</span>
                <a className="a-Section-more"></a>
                <span></span>
            </div>
            <div className="div-Section-fav-content">
                <div className="div-data">
                    <Data item = {data}></Data>
                </div>
            </div>
        </div>
    )
}

export default Section5;