import {useState, useEffect} from "react"
import './collegeList.css';
import { UserOutlined } from '@ant-design/icons';
import {Badge, Avatar} from 'antd';
const CollegeList = ({dataSource=[]}) => {
    const testData = [
        {src: "#",
         userName: "test1",
         cid: 1,
        },
         {src: "#",
         userName: "test2",
         cid: 2,
        },
         {src: "#",
         userName: "test3",
         cid: 3,
        },
         {src: "#",
         userName: "test4",
         cid: 4,
        },
         {src: "#",
         userName: "test5",
         cid: 5
        },
         {src: "#",
         userName: "test6",
         cid: 6
        },
        {src: "#",
        userName: "test7",
        cid: 7
        },
        {src: "#",
        userName: "test8",
        cid: 8
        },
        ];
    return (
        <>
            <ul>
                {testData.map(
                    (item, index) => {
                        return (
                            <li key={item.cid}>
                                <div style={{height: "46px", textAlign: "center", paddingTop: "8px", marginBottom: "16px"}}>
                                <Badge dot={true} offset={[-6, 42]} style={{width:"8px", height:"8px"}}>
                                    {/* <Avatar shape="circle" src={item.avatar} /> */}
                                    <Avatar size={48} shape="circle" icon=<UserOutlined/> />
                                </Badge>
                                </div>
                                <div style={{textAlign: "center", height: "46px"}}>
                                    <span>{item.userName}</span>
                                </div>
                            </li>
                        )
                    }
                )}
            </ul>
        </>
    )
}

export default CollegeList