import {
    Button,
    Checkbox,
    Col,
    Form,
    Input,
    Row,
    Select,
  } from 'antd';
  import { useNavigate } from 'react-router-dom';
  import Axios from 'axios';
  const { Option } = Select;
  const formItemLayout = {
    labelCol: {
      xs: {
        span: 24,
      },
      sm: {
        span: 8,
      },
    },
    wrapperCol: {
      xs: {
        span: 24,
      },
      sm: {
        span: 16,
      },
    },
  };
  const tailFormItemLayout = {
    wrapperCol: {
      xs: {
        span: 24,
        offset: 0,
      },
      sm: {
        span: 16,
        offset: 8,
      },
    },
  };

  const handleValidator = (rule, val, callback) => {
    Axios.post('http://localhost:8080/s2/loginVerify',{
      nickName: val
    }).then(resp => {
      
      const statue = resp.data;
      console.log(statue);
      if(statue === false) {
      callback("");
    };
      callback();
    });
    
    
  }
  const Register = () => {
    const navigate = useNavigate();
    const [form] = Form.useForm();
    const onFinish = (values) => {
      Axios.post('http://localhost:8080/s2/register', {
        nickName: values.nickname,
        password: values.password,
        gender: values.gender,
        schoolName: values.school,
        collegeName: values.college,
      }).then(resp => {
        const statue = resp.data;
        if(statue === "Succeed") {
          navigate('/login');
        }
        else alert('')
      }).catch(err => {
        alert('')
        console.log(err);
      });
    };


    return (
      <Form
        {...formItemLayout}
        form={form}
        name="register"
        onFinish={onFinish}
        initialValues={{
          residence: ['zhejiang', 'hangzhou', 'xihu'],
          prefix: '86',
        }}
        style={{
          maxWidth: 600,
          marginLeft: '300px',
          paddingTop: '50px'
        }}
        scrollToFirstError="true"
      >
  
       <Form.Item
          name="nickname"
          label="Nickname"
          tooltip="What do you want others to call you?"
          // messageVariables=""
          rules={[
            {
              required: true,
              message: 'Please input your nickname!',
            },
            {
              whitespace: true,
              message: ''
            },
            {
              max: 10,
              message: '10'
            },
            {
              validator: handleValidator
            }
          ]}
          validateTrigger={['onBlur']}
        >
          <Input placeholder=''/>
        </Form.Item>

        <Form.Item
          name="password"
          label="Password"
          rules={[
            {
              required: true,
              message: 'Please input your password!',
            },
          ]}
          hasFeedback
        >
          <Input.Password />
        </Form.Item>
  
        <Form.Item
          name="confirm"
          label="Confirm Password"
          dependencies={['password']}
          hasFeedback
          rules={[
            {
              required: true,
              message: 'Please confirm your password!',
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('password') === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error('The two passwords that you entered do not match!'));
              },
            }),
          ]}
        >
          <Input.Password />
        </Form.Item>
   
        <Form.Item
          name="gender"
          label="Gender"
          rules={[
            {
              required: true,
              message: 'Please select gender!',
            },
          ]}
        >
          <Select placeholder="select your gender">
            <Option value="male">Male</Option>
            <Option value="female">Female</Option>
            <Option value="other">Other</Option>
          </Select>
        </Form.Item>

        <Form.Item
          name="school"
          label="School"
          rules={[
            {
              required: true,
              message: 'Please select school!',
            },
          ]}
        >
          <Select placeholder="select your school">
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
          </Select>
        </Form.Item>

        <Form.Item
          name="college"
          label="College"
          rules={[
            {
              required: true,
              message: 'Please select College!',
            },
          ]}
        >
          <Select placeholder="select your College">
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
          </Select>
        </Form.Item>

  
        <Form.Item
          name="agreement"
          valuePropName="checked"
          rules={[
            {
              validator: (_, value) =>
                value ? Promise.resolve() : Promise.reject(new Error('Should accept agreement')),
            },
          ]}
          {...tailFormItemLayout}
        >
          <Checkbox>
            I have read the <a href="">agreement</a>
          </Checkbox>
        </Form.Item>
        <Form.Item {...tailFormItemLayout}>
          <Button type="primary" htmlType="submit">
            Register
          </Button>
        </Form.Item>
      </Form>
    );
  };
  export default Register;