import Video from './Video';
import './VideoBox.css';


const VideoBox = () => {
    const box = new Array(10).fill(<Video/>);
   
    console.log(box);
    let i = 1;
    return (
    <ul className="VideoBox">
      {box.map(item => (
        <li key ={i++}>{item}</li>
      ))}
    </ul>
    )
}

export default VideoBox
