import React from "react";
import './publish.css';
import UploadComponent from "./upload";
import Axios from "axios";
import {Button} from 'antd';
import { useState, useEffect } from "react";
import SVG from "../svg";





const Publish = () => {
    const [content, setContent] = useState(""); 
    const [imageUrl, setImageUrl] = useState("#");
    const [topic, setTopic] = useState(null);
    // useEffect (initScript);
    const publish = () => {
        const object = {
            "content" : content,
            "images" : imageUrl,
            "topic" : topic,
        };
        Axios.post("http://localhost:8080/s2/publish", object)
        .then((resp) => console.log(resp))
        .catch((error) => console.log(error));
    }

    const getImageUrl = (url) => {
        setImageUrl(url);
    }
    return (    
        <>
        
            <div className="border">
            <div className="body" style={{}}>
                <div className="selectTopic">
                    <button style = {
                        {'border-radius': '10px',
                         'border-style' : 'none',
                        'padding-left' : '15px',
                        'padding-right' : '10px',
                        'font-size' : '14px',
                        // 'color' : 'grey',
                        'margin-bottom' : '10px',
                        }
                    }></button>
                </div>
                <div className="inputContent" style={
                        {
                            'margin-bottom' : '20px',  
                        }
                    }>
                    <input placeholder="" style={
                        { 
                            'border-style' : 'none',
                            'padding' : '5px',
                        } 
                    } 
                    value = {content} 
                    onChange = {(e) => { setContent(e.target.value)} }                         
                    onFocus = {(e) => {e.target.placeholder = " "}}
                    onBlur = {(e) => {e.target.placeholder = ""}}
                    ></input>
                </div>
                <div className="Picture">
                    <UploadComponent getImageUrl = {getImageUrl}/>
                </div>
                <ul className="Functions" style={
                        {
                            'height' : '60px',
                            'padding' : 'auto 0',
                            'list-style' : 'none',
                        }
                    }>
                    <li className="left">

                        <SVG iconName="#icon-biaoqing" ></SVG>
                    
                    </li>
                    <li className="left">
                        <SVG iconName="#icon-tupian"></SVG>
                    </li>
                    <li className="left">
                        <SVG iconName="#icon-aite"></SVG>
                    </li>
                    <li className="left"></li>
                    <li className="left"></li>
                    <li className="right">
                        <Button className="publishButton" style = {{
                            margin: 0,
                            padding: 0,
                            width: "50px", 
                            height: "32px",
                            backgroundColor: "#00affe",
                            borderRadius: "5px",
                            font: "14px",
                            color: "white",
                            textAlign: "center",
                            borderStyle: "none" 
                        }} onClick = {publish}></Button>
                    </li>
                    <li className="right">
                        <SVG iconName="#icon-luosi1"></SVG>
                    </li>
                    <li className="right">1</li>
                </ul>

            </div>
        </div>
        </> 
    )
}

export default Publish;