import {useEffect, useState} from 'react';

const Svg = ({color="#00AEEC", iconName}) => {
    const [iconColor, setIconColor] = useState();

    const onClick = () => {

        setIconColor( iconColor === color ? "black" : color);
    }
    return (
        <>
            <svg className="icon" aria-hidden="true" style={{color: iconColor}} onClick={onClick}>
                <use xlinkHref={iconName} />
            </svg>
        </>
    )

}

// Svg.defaultprops = {
//     color : "#00AEEC"
// }

export default Svg