
import React from 'react';
import CommentList from '../../components/forum/List';
import './index.css';
import Publish from '../../components/forum/publish.jsx';
import CollegeList from '../../components/forum/collegeList/collegeList';
import SelectButton from '../../components/forum/selectButton/buttonList.jsx';
import FriendList from '../../components/forum/FriendList/friendList';

const liStyle = {
  float: "left",
  width: "39px",
  height: "40px",
  overflow: "hidden"
}

const Forum = () => {
    
    return (
      <div style={{width: "1190px", minHeight: 700 ,margin: "0 auto", padding: '8px 0'}}>
        <div className='SiderLeft'>
          <div
              className='userInfo'
              style={{
                width: "228px",
                boxSizing: "border-box",
                height: '144px',
                backgroundColor: 'white',
                borderRadius: '5px',
                marginBottom : '10px',
                padding: "16px 20px",
            }}>
            <div style={{ width:"196px", height: "48px", marginBottom: "17px",  overflow: "hidden"}}>
              <di style={{float:"left",}}>
                <img alt="Avator" />
              </di>
              <div style={{float:"left",}}>
                <div>UserName</div>
                <span>Lv4</span>
              </div>
            </div>
            <ul style={{listStyle: "none", width: "196px", height: "39px", overflow:"hidden"}}>
              <li style={liStyle}>
                <div>Number</div>
                <div></div>
              </li>
              <li style={liStyle}></li>
              <li style={liStyle}>
              <div>Number</div>
                <div></div>
              </li>
              <li style={liStyle}></li>
              <li style={liStyle}>
              <div>Number</div>
                <div></div>
              </li>
            </ul>
          </div>
            <ul 
              className='subject'
              style={{
              width : '228px',
              height: '623px',
              backgroundColor: 'white',
              borderRadius: '5px',
              padding : '16px 4px 8px',
              listStyle : 'none',
              overflow: 'hidden',
              }}>
              <div>
                <div style={{margin: "0 12px 0 7px", width: "281px", height: "32px"}}></div>
                {/* return */}
                <li style={{margin: "4px 0", width: "300px", height: "55px", borderRadius: "5px"}}>
                  <div className="li-Infor::before"></div>
                  <div className="li-Infor" style={{margin: "3px 12px 0 32px", width: "135px", height: "43px", overflow: "hidden"}}>
                    <div style={{height: "18px", overflow: "hidden"}}>subjectTitle</div>
                    <div style={{height: "18px", overflow: "hidden"}}>subjectInformation</div>
                  </div>
                </li>
              </div>
            </ul>
        </div>
      
        <div className='ForumContainer' style={{float: "left"}}>
          <Publish></Publish>
          <div className='CollegeList'>
            <CollegeList></CollegeList>
          </div>
          <div className='SelectButton' style={{textAlign: "center"}}>
            <SelectButton></SelectButton>
          </div>
          <ul className='CommentList'>
            <CommentList></CommentList>
          </ul> 
        </div>
        <div className='SiderRight' style={{height: "700px", width: "308px"}}>
          <div className='image?' style={{width: "308px", height: "160px", borderRadius: "5px", marginBottom: "10px", backgroundColor: "white", overflow: "hidden"}}>
            <img src="hutao.jpg" width="308px" height="160px"></img>
          </div>
          <FriendList></FriendList>
        </div>

      </div>      
    )
}

export default Forum;