import { Outlet, Link, useLocation} from "react-router-dom";
import {Layout,  Menu} from 'antd';
const {Header} = Layout;


const Layouts = () => {
    const location = useLocation();
    const selectedKeys = location.pathname;
    return (
        <div>
          <div style={{
                marginTop: 64,
                minHeight: '700px',
                backgroundImage: 'url("IMG_6787.jpg")',
                backgroundSize: "100% 100%",
              }}>
            <div className="LayoutsHeader" style={{ position: "fixed"}}>
              <Menu
                theme="light"
                mode="horizontal"
                defaultSelectedKeys={selectedKeys}
                style = {{
                  height: 64,
                  width: 1519.2,
                  lineHeight: 4.5,
                }}
              >
                  <Menu.Item  key="/" ><Link to="/"></Link></Menu.Item> 
                  <Menu.Item  key="2" ><Link to></Link></Menu.Item> 
                  <Menu.Item  key="3" ><Link></Link></Menu.Item> 
                  <Menu.Item  key="/columnist" ><Link to="/columnist"></Link></Menu.Item>
                  <Menu.Item  key="5" ><Link></Link></Menu.Item> 
                  <Menu.Item  key="6" ><Link></Link></Menu.Item> 
                  <Menu.Item  key="7" ><Link></Link></Menu.Item>
                  <Menu.Item  key={8} ><Link></Link></Menu.Item> 
                  <Menu.Item  key={9} ><Link></Link></Menu.Item> 
                  <Menu.Item  key={10} ><Link></Link></Menu.Item>
                  <Menu.Item  key={11} ><Link></Link></Menu.Item> 
                  <Menu.Item  key={12} ><Link></Link></Menu.Item> 
                  <Menu.Item  key="/forum1" ><Link to="/forum"></Link></Menu.Item>
                  <Menu.Item  key="/register" ><Link to="register"></Link></Menu.Item> 
                  <Menu.Item  key="/login" ><Link to='/login'></Link></Menu.Item> 
                  <Menu.Item  key="/home" ><Link to="/home"></Link></Menu.Item> 
              </Menu>
            </div>
            <Outlet/>
          </div>
        </div>
      );
}

export default Layouts;