const CommentList = () => {
  
  const [moment, setMoment] = useState([]);
  // const [img, setImg] = useState('img');
  // const [topic, setTopic] = useState('topic');
  // const [userName, setUserName] = useState('userName');
  // const [avatar, setAvatar] = useState("avatar");
  useEffect(
      () => {
          const getMoment = async () => {
            await Axios.get(queryMoment).then((resp) => {console.log(resp.data[0]); setMoment(resp.data);}).catch((error) => console.log(error))
          }
          getMoment();
          Axios.get(queryMoment).then((resp) => {console.log(resp.data[0]); setMoment(resp.data);}).catch((error) => console.log(error))

      },[])

  const data = Array.from({
    length: moment.length
  }).map((_, i) => {
    return ({
    href: '#',
    userName: moment[i].userName,//userName
    avatar: `https://joesch.moe/api/v1/random?key=${i}`,
    topic: moment[i].topic,
    content: moment[i].content,
    collectionNum: moment[i].collectionNum,
    commentNum: moment[i].commentNum,
    commend: moment[i].commend,
    imageUrl: moment[i].imageUrl,
    mid: moment[i].mid,//
    score: moment[i].score,//
      })
    });
    
  const IconText = ({ icon, text }) => (//
    <Space>
      {React.createElement(icon)}
      {text}
    </Space>
  );
  return  (<List
    itemLayout="vertical"
    size="large"
    pagination={{
      onChange: (page) => {
        console.log(page);
      },
      pageSize: 10,
    }}
    dataSource={data}
    footer={
      <div>
        <b>ant design</b> footer part
      </div>
    }
    renderItem={(item) => (
      <List.Item
        key={item.mid} 
        actions={[
          <IconText icon={StarOutlined} text={item.collectionNum} key="list-vertical-star-o" />,
          <IconText icon={LikeOutlined} text={item.commend} key="list-vertical-like-o" />,
          <IconText icon={MessageOutlined} text={item.commentNum} key="list-vertical-message" />,
        ]}
        extra={
          <img
            width={250}
            alt="logo"
            // src="https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png"
            src={item.imageUrl}
            // src="https://mys2-1312072534.cos.ap-guangzhou.myqcloud.com/Test_Account/2023-05-02/1683020606422test.jpg"
            // onClick = {() => {}}
          />
        }
      >
        <List.Item.Meta
          avatar={<Avatar src={item.avatar} />}
          title={<a href={item.href}>{item.userName}</a>}
          description={item.topic}
        />
        {item.content}
      </List.Item>
    )}
  />
)
};
export default CommentList;